.line-through {
  text-decoration: line-through;
  user-select: none; }

.recordListing h2 {
  font-size: 3.5em;
  font-family: Roboto Slab, serif;
  margin-bottom: 0.35714em;
  color: #FF0000;
  line-height: 1.2em; }

.recordListing h3 {
  font-size: 2.2em;
  font-family: Roboto Slab, serif;
  margin-bottom: 1.13636em;
  color: #6B6A6A;
  text-align: center; }
  .recordListing h3 ~ p {
    font-size: 1.4em;
    font-family: Roboto Mono, monospace;
    margin-bottom: 0.89286em;
    color: #6B6A6A;
    text-align: center; }
    .recordListing h3 ~ p a {
      color: #636363;
      text-decoration: underline;
      -webkit-text-decoration-skip: edges;
      text-decoration-skip: edges;
      -webkit-text-decoration-color: #FF001F;
      text-decoration-color: #FF001F; }

.recordListing h4 {
  font-size: 2.2em;
  font-family: Roboto Slab, serif;
  margin-bottom: 1.13636em;
  color: #6B6A6A; }

.recordListing p {
  font-size: 2.2em;
  font-family: Roboto Mono, monospace;
  margin-bottom: 0.56818em;
  color: #6B6A6A;
  line-height: 1.3em; }

.recordListing .hide {
  display: none; }

.recordListing .show {
  display: inherit; }

@media screen and (min-width: 979px) {
  .recordListing {
    width: 804px;
    margin: 0 auto; }
    .recordListing img {
      width: 100%; } }

.record {
  text-align: center;
  display: inline-block;
  margin: 50px 0 10px 0;
  width: 100%; }

@media screen and (min-width: 979px) {
  .record {
    margin: 50px 0 50px 0;
    text-align: center; } }

.description p {
  font-size: 1.8em;
  font-family: Roboto Mono, monospace;
  margin-bottom: 1.38889em;
  font-weight: 300; }

.categories {
  background: transparent;
  text-align: center;
  margin: 50px 0 0 0; }
  .categories ul li {
    color: #959595;
    font-size: 1.6em;
    font-family: Roboto Mono, monospace;
    margin-bottom: 0;
    padding: 5px 10px;
    margin: 0 5px 10px 0;
    border: 1px solid #bdbdbd;
    display: inline-block; }

.videoContainer {
  text-align: center;
  margin: 0 0 20px 0; }

.videoSineWave {
  background: transparent; }

.recommendedVideos {
  border-top: 1px solid #E5E5E5;
  padding: 20px 0 0 0;
  text-align: center; }

.pageUnit {
  background: transparent; }

.videoUnits {
  background: transparent; }

.videoCaption {
  display: block;
  font-size: 2em;
  font-family: Roboto Mono, monospace;
  margin-bottom: 1.25em;
  color: #696969; }

@media screen and (min-width: 767px) {
  .recordListing {
    padding: 40px 0; }
  .recordListing {
    min-height: 180px;
    padding: initial; } }
